"use client";

import React, { useState } from "react";
import { useSelector } from "react-redux";
import { User, ShoppingCart, Heart, ClipboardList, MapPin, Menu } from "lucide-react";
import dynamic from "next/dynamic";
import { Button } from "@/components/ui/button";

// Lazy load sections
const Wishlist = dynamic(() => import("../../wishlist/page"), { ssr: false });
const OrderPage = dynamic(() => import("../../orders/page"), { ssr: false });
const Cart = dynamic(() => import("../../cart/Cart"), { ssr: false });
const EditProfile = dynamic(() => import("../profile/manage/page"), { ssr: false });
const AddressManagement = dynamic(() => import("../profile/address/page"), { ssr: false });

const UserProfile = () => {
  const { user } = useSelector((state) => state.userData);
  const [activeSection, setActiveSection] = useState("editProfile");
  const [sidebarOpen, setSidebarOpen] = useState(false);

  return (
    <div className="flex min-h-screen bg-white relative">
      {/* Sidebar Navigation */}
      <nav
        className={`fixed md:relative top-0 left-0 h-full bg-white text-gray-800 shadow-lg transition-transform ${
          sidebarOpen ? "translate-x-0" : "-translate-x-full"
        } md:translate-x-0 md:w-64 w-64 z-50 p-4`}
      >
        {/* Sidebar Header with Toggle for Mobile */}
        <div className="flex items-center justify-between md:hidden">
          <h2 className="text-lg font-semibold text-gray-700">Menu</h2>
          <Button variant="ghost" onClick={() => setSidebarOpen(false)}>
            âœ–
          </Button>
        </div>

        {/* User Info */}
        <div className="hidden md:flex items-center mb-6">
          <div className="w-12 h-12 rounded-full overflow-hidden bg-gray-300 flex items-center justify-center">
            {user?.profile_pic ? (
              <img src={user.profile_pic} alt="Profile" className="w-full h-full object-cover" />
            ) : (
              <span className="text-2xl font-bold text-gray-600">{user?.name?.charAt(0)}</span>
            )}
          </div>
          <h2 className="ml-2 text-lg font-semibold text-gray-700">{user?.name}</h2>
        </div>

        {/* Sidebar Menu */}
        <ul className="space-y-2 flex flex-col items-start w-full">
          {[
            { label: "Profile", icon: <User />, section: "editProfile" },
            { label: "Wishlist", icon: <Heart />, section: "wishlist" },
            { label: "Orders", icon: <ClipboardList />, section: "orders" },
            { label: "Address", icon: <MapPin />, section: "editAddress" },
            { label: "Cart", icon: <ShoppingCart />, section: "cart" },
          ].map(({ label, icon, section }) => (
            <li key={section} className="w-full">
              <Button
                variant="ghost"
                className={`flex items-center w-full p-3 rounded-lg hover:bg-gray-100 ${
                  activeSection === section ? "bg-gray-200" : ""
                }`}
                onClick={() => {
                  setActiveSection(section);
                  setSidebarOpen(false);
                }}
              >
                {icon} <span className="ml-2">{label}</span>
              </Button>
            </li>
          ))}
        </ul>
      </nav>

      {/* Mobile Menu Button */}
      <Button
        variant="ghost"
        className="md:hidden fixed top-4 left-4 z-50 bg-white shadow-lg p-2 rounded-lg"
        onClick={() => setSidebarOpen(true)}
      >
        <Menu className="w-6 h-6" />
      </Button>

      {/* Main Content */}
      <div className="flex-1 bg-gray-50 w-full overflow-y-auto p-3 md:p-6">
        {activeSection === "editProfile" && <EditProfile />}
        {activeSection === "editAddress" && <AddressManagement />}
        {activeSection === "wishlist" && <Wishlist />}
        {activeSection === "orders" && <OrderPage />}
        {activeSection === "cart" && <Cart />}
      </div>
    </div>
  );
};

export default UserProfile;
